export default {
    data() {
        return {
            n: true,
            coupon: '',
            type: '',
            news: '',
            sum: '',
            discount: '',
            len: [],
            tabCurrentIndex: 0,
            couponTip: '',
            nums: '1',
            navList: [
                {
                    state: 0,
                    text: '全部(0)'
                },
                {
                    state: 1,
                    text: '存力满减券(0)'
                },
                {
                    state: 2,
                    text: '存力打折券(0)'
                },
                {
                    state: 3,
                    text: '转出(0)'
                }
            ],
            all: true,
            all1: true,
            all2: true,
            flag: false
        };
    },
    onShow() {
        uni.request({
            url: this.url + 'mydiscount/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success: res => {
                this.type = res.data.data[0].join();
                if (this.type == 1) {
                    this.navList.length = 3;
                }
                var coupon_total = 0,
                    news_total = 0,
                    discount_total = 0;
                for (let i = 0; i < res.data.data[1].length; i++) {
                    let item = res.data.data[1][i],
                        lens = parseInt(item.lens);
                    coupon_total += lens;
                    if (item.type === 1) {
                        news_total += lens;
                    } else if (item.type === 1) {
                        discount_total += lens;
                    }
                }
                this.coupon = res.data.data[1];
                this.news = res.data.data[1].filter(val => {
                    return val.type === 1;
                });
                this.discount = res.data.data[1].filter(val => {
                    return val.type === 2;
                });
                if (this.coupon.length == 0) {
                    this.all = false;
                }
                if (this.news.length == 0) {
                    this.all1 = false;
                }
                if (this.discount.length == 0) {
                    this.all2 = false;
                }
                for (let i = 0; i < this.coupon.length; i++) {
                    this.couponType = this.coupon[i].type;
                    var l = this.coupon[i].lens;
                    this.len.push(l);
                    var sum = 0;
                    for (var j = 0; j < this.len.length; j++) {
                        sum += parseInt(this.len[j]);
                        this.sum = sum;
                    }
                }
                for (let i = 0; i < this.news.length; i++) {
                    var t = this.news[i].start;
                    var t1 = t.substr(0, 10);
                    this.news[i].start = t1;
                    var up = this.news[i].end;
                    var up1 = up.substr(0, 10);
                    this.news[i].end = up1;
                }
                for (let i = 0; i < this.discount.length; i++) {
                    var t = this.discount[i].start;
                    var tt = this.discount[i].end;
                    var t1 = t.substr(0, 10);
                    this.discount[i].start = t1;
                    var up = this.discount[i].end;
                    var up1 = up.substr(0, 10);
                    this.discount[i].end = up1;
                }
            }
        });
        uni.request({
            url: this.url + 'discountrecode/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success: res => {
                this.couponTip = res.data.data;
                if (this.couponTip == 0) {
                    this.flag = true;
                }
            }
        });
    },
    methods: {
        changeTab(e) {
            this.tabCurrentIndex = e.target.current;
        },
        tabClick(index) {
            var that = this;
            that.tabCurrentIndex = index;
        },
        transfer(item) {
            uni.request({
                url: this.url + 'discounttransfer/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success: res => {
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../coupon-transfer/coupon-transfer?t=' + item
                        });
                    }
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '用户未实名认证或未设置资金密码',
                            duration: 3000,
                            icon: 'none'
                        });
                    }
                }
            });
        },
        next: function() {
            this.n = false;
        },
        back: function() {
            this.n = true;
        }
    }
};
